import { NextSeo } from 'next-seo';
import React, { useContext } from 'react';
import { AppContext } from '@components/common/context/AppProvider';
import { Card, CardContent, Stack, Typography } from '@mui/material';
import { cleanUnderscore, commaNotation, notateNumber, prefix } from '@utility/helpers';
import { CardTitleAndValue } from '@components/common/styles';
import Tabber from '@components/common/Tabber';
import Upgrades from '@components/account/Worlds/World2/Kangaroo/Upgrades';
import Tooltip from '@components/Tooltip';
const Kangaroo = () => {
  const {
    state
  } = useContext(AppContext);
  const {
    kangaroo
  } = state?.account || {};
  const notation = val => val < 9999999 ? commaNotation(Math.ceil(val)) : notateNumber(val, 'Big');
  return <>
    <NextSeo title="Poppy The Kangaroo | Idleon Toolbox" description="Keep track of your kangaroo upgrades and progress" data-sentry-element="NextSeo" data-sentry-source-file="kangaroo.jsx" />
    <Stack direction={'row'} gap={2} flexWrap={'wrap'} data-sentry-element="Stack" data-sentry-source-file="kangaroo.jsx">
      <CardTitleAndValue cardSx={{
        my: 1,
        height: 'fit-content'
      }} stackProps={{
        flexDirection: 'row',
        gap: '16px',
        flexWrap: 'wrap'
      }} data-sentry-element="CardTitleAndValue" data-sentry-source-file="kangaroo.jsx">
        <Section title={'Fish'} value={notation(kangaroo?.fish)} icon={'etc/KFish.png'} data-sentry-element="Section" data-sentry-source-file="kangaroo.jsx" />
        <Section title={'Fish/m'} value={notation(kangaroo?.bonuses?.[0]?.bonus)} icon={'etc/KFish.png'} data-sentry-element="Section" data-sentry-source-file="kangaroo.jsx" />
        <Section title={'Fish/h'} value={notation(kangaroo?.bonuses?.[0]?.bonus * 60)} icon={'etc/KFish.png'} data-sentry-element="Section" data-sentry-source-file="kangaroo.jsx" />
        <Section title={'Next upg.'} value={kangaroo?.nextLvReq > 0 ? `${notateNumber(kangaroo?.progress)}/${notateNumber(kangaroo?.nextLvReq)}` : 'Done'} icon={'etc/KFish.png'} data-sentry-element="Section" data-sentry-source-file="kangaroo.jsx" />
      </CardTitleAndValue>
      <CardTitleAndValue cardSx={{
        my: 1,
        height: 'fit-content'
      }} stackProps={{
        flexDirection: 'row',
        gap: '16px',
        flexWrap: 'wrap'
      }} data-sentry-element="CardTitleAndValue" data-sentry-source-file="kangaroo.jsx">
        <Section title={'Tars'} value={notation(kangaroo?.tarFishOwned)} icon={'etc/KTar.png'} data-sentry-element="Section" data-sentry-source-file="kangaroo.jsx" />
        <Section title={'Tar/h'} value={notation(3600 / kangaroo?.tarFishRate)} icon={'etc/KTar.png'} data-sentry-element="Section" data-sentry-source-file="kangaroo.jsx" />
      </CardTitleAndValue>
      <CardTitleAndValue cardSx={{
        my: 1,
        height: 'fit-content'
      }} stackProps={{
        flexDirection: 'row',
        gap: '16px',
        flexWrap: 'wrap'
      }} data-sentry-element="CardTitleAndValue" data-sentry-source-file="kangaroo.jsx">
        <Section title={'Progress'} value={`${notation(kangaroo?.shinyProgress)}%`} icon={'etc/KShiny.png'} data-sentry-element="Section" data-sentry-source-file="kangaroo.jsx" />
        <Section title={'Shiny/m'} value={`${notation(kangaroo?.shinyRatePercent)}%`} icon={'etc/KShiny.png'} data-sentry-element="Section" data-sentry-source-file="kangaroo.jsx" />
      </CardTitleAndValue>
      <Stack direction={'row'} gap={2} flexWrap={'wrap'} data-sentry-element="Stack" data-sentry-source-file="kangaroo.jsx">
        {kangaroo?.bonuses.map(({
          name,
          bonus,
          percentage
        }, index) => {
          if (index === 0) return;
          bonus = Math.round(10 * bonus) / 10;
          return <CardTitleAndValue cardSx={{
            my: 1
          }} key={name} title={name} value={`${!percentage ? '+' : ''}${bonus}${percentage ? '%' : ''}`} icon={`etc/Kangaroob_${index}.png`} />;
        })}
      </Stack>
    </Stack>
    <Tabber tabs={['Upgrades', 'Tar Upgrades', 'Bonuses']} data-sentry-element="Tabber" data-sentry-source-file="kangaroo.jsx">
      <Upgrades upgrades={kangaroo?.upgrades} data-sentry-element="Upgrades" data-sentry-source-file="kangaroo.jsx" />
      <Upgrades upgrades={kangaroo?.tarUpgrades} isTar data-sentry-element="Upgrades" data-sentry-source-file="kangaroo.jsx" />
      <Stack gap={5} data-sentry-element="Stack" data-sentry-source-file="kangaroo.jsx">
        <Stack data-sentry-element="Stack" data-sentry-source-file="kangaroo.jsx">
          <Typography variant={'h5'} mb={1} data-sentry-element="Typography" data-sentry-source-file="kangaroo.jsx">Multi</Typography>
          <CardTitleAndValue cardSx={{
            my: 1,
            height: 'fit-content'
          }} stackProps={{
            flexDirection: 'row',
            gap: '16px',
            flexWrap: 'wrap'
          }} data-sentry-element="CardTitleAndValue" data-sentry-source-file="kangaroo.jsx">
            <Section title={'Total multi'} value={`${kangaroo?.totalMulti.replace('.00', '')}x`} data-sentry-element="Section" data-sentry-source-file="kangaroo.jsx" />
            {kangaroo?.allMultipliers?.map(({
              multi,
              amount
            }, multiIndex) => <Section key={'multi' + multiIndex} title={amount} icon={`data/RooFishS${multiIndex}.png`} value={multi !== '1' ? `${multi}x` : ''} />)}
          </CardTitleAndValue>
        </Stack>
        <Stack data-sentry-element="Stack" data-sentry-source-file="kangaroo.jsx">
          <Typography variant={'h5'} mb={1} data-sentry-element="Typography" data-sentry-source-file="kangaroo.jsx">Reset bonuses</Typography>
          <Stack direction={'row'} gap={2} flexWrap={'wrap'} data-sentry-element="Stack" data-sentry-source-file="kangaroo.jsx">
            {kangaroo?.resetBonuses.map(({
              desc,
              level
            }, index) => {
              return <ResetCard key={'reset' + index} level={level} desc={cleanUnderscore(desc)} icon={`data/RooReset${index}.png`} />;
            })}
          </Stack>
        </Stack>
        <Stack data-sentry-element="Stack" data-sentry-source-file="kangaroo.jsx">
          <Typography variant={'h5'} mb={1} data-sentry-element="Typography" data-sentry-source-file="kangaroo.jsx">Mega fish</Typography>
          <Stack direction={'row'} gap={2} flexWrap={'wrap'} data-sentry-element="Stack" data-sentry-source-file="kangaroo.jsx">
            {kangaroo?.megaFish?.map(({
              description,
              unlocked,
              amount,
              totalBonus
            }, index) => {
              return <CardTitleAndValue cardSx={{
                my: 1
              }} value={amount > 0 ? amount : ''} tooltipTitle={cleanUnderscore(description.replace('{', totalBonus))} key={'mega' + index} icon={`data/RooMG${index}.png`} imgStyle={{
                width: 32,
                opacity: unlocked ? 1 : .5
              }} imgOnly />;
            })}
          </Stack>
        </Stack>
      </Stack>
    </Tabber>
  </>;
};
const Section = ({
  title,
  value,
  icon,
  sx,
  iconStyle,
  fontSize
}) => {
  return <div style={{
    ...sx
  }} data-sentry-component="Section" data-sentry-source-file="kangaroo.jsx">
    <Typography sx={{
      fontSize: 14
    }} color="text.secondary" gutterBottom data-sentry-element="Typography" data-sentry-source-file="kangaroo.jsx">{title}</Typography>
    <Stack direction="row" gap={1} data-sentry-element="Stack" data-sentry-source-file="kangaroo.jsx">
      {icon ? <img style={{
        objectFit: 'contain',
        ...iconStyle
      }} src={`${prefix}${icon}`} alt="" /> : null}
      <Typography sx={{
        fontSize
      }} data-sentry-element="Typography" data-sentry-source-file="kangaroo.jsx">{value}</Typography>
    </Stack>
  </div>;
};
const ResetCard = ({
  level,
  desc,
  icon
}) => {
  return <Tooltip title={desc} data-sentry-element="Tooltip" data-sentry-component="ResetCard" data-sentry-source-file="kangaroo.jsx">
    <Card data-sentry-element="Card" data-sentry-source-file="kangaroo.jsx">
      <CardContent data-sentry-element="CardContent" data-sentry-source-file="kangaroo.jsx">
        <Stack direction={'row'} gap={1} data-sentry-element="Stack" data-sentry-source-file="kangaroo.jsx">
          <img style={{
            objectFit: 'contain',
            width: 26,
            height: 26
          }} src={`${prefix}${icon}`} alt="" />
          <Typography data-sentry-element="Typography" data-sentry-source-file="kangaroo.jsx">Lv. {level}</Typography>
        </Stack>
      </CardContent>
    </Card>
  </Tooltip>;
};
export default Kangaroo;